import React, {useState} from 'react'
import '../Styles/ContactStyle.css';
import { useHistory} from 'react-router-dom';


export const Contact = () => {

  const [check, setcheck] = useState(false);
  const btns = document.querySelectorAll(".my-form button");  
  const handleCheck = ()=>{
    setcheck(!check);
    for (const btn of btns) {
      !check ? (btn.disabled = false) : (btn.disabled = true);
    }

  };
  let history = useHistory();
  const goToPreviousPath = () => {
      history.goBack()
  }



  // const checkbox = document.querySelector('.my-form input[type="checkbox"]');

  // checkbox.addEventListener("change", function() {
  //   const checked = this.checked;
  //   for (const btn of btns) {
  //     checked ? (btn.disabled = false) : (btn.disabled = true);
  //   }
  // });
  
    
    return (
        <div><form className="my-form">
        <div className="form-container">
          <h1>Get in touch!</h1>
          <ul>

            <li>
              <div className="grid grid-2">
                <input type="text" placeholder="Name" required/>  
                <input type="text" placeholder="Surname" required/>
              </div>
            </li>
            <li>
              <div className="grid grid-2">
                <input type="email" placeholder="Email" required/>  
                <input type="tel" placeholder="Phone"/>
              </div>
            </li>    
            <li>
              <textarea placeholder="Message"></textarea>
            </li>   
            <li>
              <div>
                <div className="my-checkbox">

              <input type="checkbox" style={{marginTop:"5px", width: "auto"}} checked={check} onChange={handleCheck} id=""/>
                </div>
              <label style={{marginLeft:"50px"}} htmlFor="terms">I have read and agreed with <a href="/">the terms and conditions.</a></label>
              </div>
              {/* <input type="checkbox" checked={check} onClick={handleCheck} id="terms"/> */}

            </li>  
            <li>
              <div className="grid grid-3">
                <div className="required-msg">REQUIRED FIELDS</div>
                <button className="btn-grid" type="submit" disabled>
                  <span className="back">
                    <img src="https://s3-us-west-2.amazonaws.com/s.cdpn.io/162656/email-icon.svg" alt=""/>
                  </span>
                  <span className="front">SUBMIT</span>
                </button>
                <button className="btn-grid" type="reset" disabled>
                  <span className="back">
                    <img src="https://s3-us-west-2.amazonaws.com/s.cdpn.io/162656/eraser-icon.svg" alt=""/>
                  </span>
                  <span className="front">RESET</span>
                </button> 
              </div>
            </li>    
          </ul>
        </div>
      </form>
      <div className="go-back">
      <h1><a style={{cursor:"pointer"}} href={goToPreviousPath} onClick={goToPreviousPath}><span>&#8592;</span> Go Back</a></h1>

      </div>

        </div>
    )
}
